import React ,{ Component} from 'react'
import './style.css'
class Style extends Component{
  state = {
    activeIndex : 0,
    styles : [
      {
        style: 'iphone5'
      },
      {
        style: 'iphone6'
      },
      {
        style: 'iphone7'
      }
    ]
  }
  handleClick = (i) =>{
    console.log(i)
    this.setState({
      activeIndex : i
    })
  }
  render(){
    const styleList = this.state.styles.map((item,i) =>(
      <a href="javaScript:;" onClick ={() => this.handleClick(i)}
      key = {i}
      className={`style ${this.state.activeIndex ===i && 'active'}`}>
      {item.style}
      </a>
    )
  )
    return (
      <div className="style">
      {styleList}
      </div>
    )
  }
}
export default Style



