import React ,{ Component} from 'react'
import './sign-in.css'
class SignIn extends Component{
  state ={
    useName: '',
    password: ''
  }
  changeUserName = (e) =>{
    this.setState({
      userName: e.target.value
    })
  }
  changePassword = (e) =>{
    this.setState({
      password: e.target.value
    })
  }
  handleSubmit = () => {
    console.log(this.state.userName)
    // 发 axios 请求 把数据提交到服务器
    this.props.login(this.state.userName)
  }
  render(){
    const { hideLoginForm } = this.props
    const {userName, password} = this.state
    return (
      <div className="denglu ">
      <div className="log_on">
         <a href="javaScript:;" className="hide-loginform"
         onClick={hideLoginForm}
         ><img src={require("./images/x_03.jpg")} alt="" className="guanbi_t" /></a>
        <div className="logo_one">
          <img src={require("./images/logo1_03_03.png")} alt="" />
        </div>
        <div className="account">
          <a href="javaScript:;"><img src={require("./images/dl.jpg")} alt="" /></a>

          <input type="text" className="userName"
          value={userName} onChange={this.changeUserName}
          />

          <div>
            <span className="one cw">错误</span>
            <span className="one zq">正确</span>
          </div>
        </div>
        <div className="account">
          <a href="javaScript:;"><img src={require("./images/dl2_16.jpg")} alt="" /></a>

          <input type="password" className="password"
          value={password} onChange={this.changePassword}
          />

          <div>
            <span className="one cw">错误</span>
            <span className="one zq">正确</span>
          </div>
        </div>
          <a href="javaScript:;" className="dl"
          onClick = {this.handleSubmit}
          >登录</a>
        <div className="zhuce clrarfix">
          <a href="javaScript:;">忘记密码?</a>
          <a href="javaScript:;">注册</a>
        </div>
        <div className="link_a">
          <a href="javaScript:;"><img src={require("./images/QQ.png")} alt="" /></a>
          <a href="javaScript:;"><img src={require("./images/WX.png")} alt="" /></a>
          <a href="javaScript:;"><img src={require("./images/WB.png")} alt="" /></a>
        </div>
      </div>
    </div>
    )
  }
}

export default SignIn



