import React ,{ Component} from 'react'
import './number.css'

class Number extends Component{
  state ={
    count: 1
  }
  increment = (e) => {
    e.preventDefault()
    this.setState({
      count:this.state.count +1
    })
  }
  decrement = (e) =>{
    e.preventDefault()
    if(this.state.count>0){
      this.setState({
        count:this.state.count -1
      })
    }

  }
  render(){
    return (
      <div className="number">
        <a href="#" className="reduce" onClick={this.decrement}
        >-</a>
        <span className="num">{this.state.count}</span>
        <a href="#" className="add" onClick={this.increment}
        >+</a>
      </div>
    )
  }
}

export default Number



