import React ,{ Component} from 'react'
import './sizes.css'

class Sizes extends Component{
  state = {
    activeIndex : 0,
    sizes : [
      {
        size: 'XXS'
      },
      {
        size: 'XS'
      },
      {
        size: 'S'
      },
      {
        size:'M'
      },
      {
        size:'L'
      },
      {
        size:'XL'
      },
      {
        size:'XXl'
      }
    ]
  }
  handleClick = (i) =>{
    console.log(i)
    this.setState({
      activeIndex : i
    })
  }
  render(){
    const sizeList = this.state.sizes.map((item,i) =>(
      <a href="javaScript:;" onClick ={() => this.handleClick(i)}
      key = {i}

      className={`size ${this.state.activeIndex ===i && 'active'}`}>
      {item.size}
      </a>
    )
  )
    return (
      <div className="sizes">
        {sizeList}
      </div>
    )
  }
}

export default Sizes



