import React ,{ Component} from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick'
import './banner.css'
class Banner extends Component{
  render(){
     var settings = {
       dots: false,
       infinite: true,
       speed: 500,
       slidesToShow: 3,
       slidesToScroll: 1,
       autoplay:true,
       arrows:true,
       touchMove:false,
   }
   const slides = [
     <div className='dish-card-warp' key='1'>
       <div className='dish-card'>
         <img src={require("./images/lunbo_03.jpg")}/>
       </div>
     </div>,
     <div className='dish-card-warp' key='2'>
       <div className='dish-card'>
         <img src={require("./images/lunbo_05.jpg")}/>
       </div>
     </div>,
     <div className='dish-card-warp' key='3'>
       <div className='dish-card'>
         <img src={require("./images/lunbo_07.jpg")}/>
       </div>
     </div>,
   ]
   const slider = (
     <Slider {...settings}>
       {slides}
     </Slider>
   )
    return (
      <div className="banner">
        <div className="banner_on">
            {slider}
        </div>
      </div>
    )
  }
}

export default Banner



